.wrapper {
  font-family: 'Montserrat', sans-serif;

  h1 {
    white-space: nowrap;
  }

  ul {
    list-style-type: none;
    padding-left: 0;


    li {
      padding-left: 2rem;
      padding-bottom: 20px;
      background-image: url('../../../../public/assets/check-mark-button-svgrepo-com.svg');
      background-position: 0 0px;
      background-repeat: no-repeat;
    }
  }

  .text {
    text-align: start;
    margin-bottom: 20px;
  }

  a {
    margin: 0 auto;
    display: block;
    color: black;
    border: 1px solid black;
    border-radius: 10px;
    padding: 12px 20px;
    max-width: 200px;
  }

  a:hover {
    background-color: #6CF;
    border-color: white;
    color: white;
  }
}